  // console.log("fix", toFix)

  const excludeWords = ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "-", "", "", "", "", "", "", "", "", "", "", "", "-", "", "", "", "", "", "-", "-", "", "", "", "", "", "", "", "", "", "", "", "", "", "-", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""]

  export const words = freqDictionary.filter(x => excludeWords.indexOf(x) < 0 && x.indexOf("-") < 0)

  export const wordsForTask = _(words).filter(x => minLength <= x.length && x.length <= maxLength)
    .take(1000).groupBy((x: string) => x.length).value()
  export const allWords: string[] = words.filter(x => x.length >= minLength).sort()

  export type WordListWithLength = {
    byLength: typeof wordsForTask
    words: string[]
    minLength: number
    maxLength: number
  }

  export function getWords(maxLength: number): WordListWithLength {
    return {
      byLength: wordsForTask,
      words: _.flatMap(_.range(minLength, maxLength + 1), i => wordsForTask[i] || []),
      minLength,
      maxLength
    }
  }
